@import './theme.css';

/* 
  Customize theme styles
*/
.row.banner {
  background: rgba(0,0,0,0.05);
  padding: 25px 15px 5px 15px;
}
.row.banner:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255,255,255,0.8);
  z-index: -1;
}
header .banner-text h3 {
  color: #CCCCCC;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 1);
}
header .scrolldown a:hover,
header .social li a:hover {
  color: #a9e65a;
}
ul#nav li.current a { /* Header Item HighLight */
  color: #a9e65a;
}

.row.work ul{
  list-style: unset;
}

#go-top a:hover {
  background-color: #a9e65a;
}

