#portfolio .projectCard .projectCardMeta .ant-card-meta-title,
#portfolio .projectCard .projectCardMeta .ant-card-meta-description
{
  color: white !important;
}

#portfolio .projectCard .projectCardMeta .ant-card-meta-description ul{
  list-style: circle;
}
#portfolio-wrapper {
  display: flex; 
  flex-wrap: wrap; 
  justify-content: center;
}
/* Remove Row Width Limit In This Section */
#portfolio .row{
  max-width: unset!important;
}
@media screen and (min-width: 1200px){
  .ant-card.projectCard {
    margin-right: 20px;
  }
}